import { Vue, Component, Watch } from "vue-property-decorator";

@Component({
  components: {}
})
export class OrdinalMixin extends Vue {
  ordinal(num: number) {
    if (this.$i18n.locale === "de") {
      return `${num}.`;
    }
    const j = num % 10;
    const k = num % 100;
    if (j === 1 && k !== 11) {
      return `${num}st`;
    }
    if (j === 2 && k !== 12) {
      return `${num}nd`;
    }
    if (j === 3 && k !== 13) {
      return `${num}rd`;
    }
    return `${num}th`;
  }
}
