




























import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import FileBrowser from "./FileBrowser.vue";

/**
 * v-model controls open state
 */
@Component({
  components: {
    FileBrowser
  }
})
export default class FolderSelectorDialog extends Mixins() {
  @Prop({
    type: [Boolean],
    required: true
  })
  value!: boolean;

  @Watch("isOpen")
  onOpen() {
    if (this.isOpen) {
      (this.$refs.fileBrowser as FileBrowser).manuallySetFolders([]);
    }
  }

  get isOpen() {
    return this.value;
  }

  set isOpen(val) {
    this.$emit("input", val);
  }

  chosenLocations: string[] = [];

  get chosenLocation() {
    return this.chosenLocations[0] || false;
  }
}
