import { Vue, Component } from "vue-property-decorator";

const image = "mdi-file-image";
const account = "mdi-file-account";
const excel = "mdi-file-excel";
const lock = "mdi-file-lock";
const music = "mdi-file-music";
const pdf = "mdi-file-pdf";
const powerPoint = "mdi-file-powerpoint";
const video = "mdi-file-video";
const word = "mdi-file-word";
const xml = "mdi-file-xml";
const table = "mdi-file-table";
const text = "mdi-file-document";
const move = "mdi-file-move";
const cons = "mdi-console";
const zip = "mdi-zip-box";

const icons: { [i: string]: string } = {
  png: image,
  jpg: image,
  jpeg: image,
  gif: image,
  bashrc: account,
  xlsx: excel,
  xls: excel,
  lock,
  mp3: music,
  wav: music,
  m4a: music,
  pdf,
  pptx: powerPoint,
  ppt: powerPoint,
  mp4: video,
  webm: video,
  docx: word,
  doc: word,
  xml,
  html: xml,
  htm: xml,
  php: xml,
  db: table,
  txt: text,
  log: text,
  yml: text,
  yaml: text,
  md: text,
  ini: table,
  lnk: move,
  exe: cons,
  dll: cons,
  jar: cons,
  bat: cons,
  sh: cons,
  zip,
  gz: zip,
  "7z": zip
};

@Component({
  components: {}
})
export class FileIcons extends Vue {
  $getFileIcon(name: string) {
    const ext = name.split(".").pop() || "";
    return icons[ext] || "mdi-file";
  }
}
