












































































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { routerModule, repositoriesModule } from "@/store/modules";
import Timeline from "@/components/Timeline.vue";
import SnapshotsList from "@/components/repositories/SnapshotsList.vue";
import { ISnapshot, ISnapshotDetails } from "@/store/api";
import { parse } from "date-fns";
import { DateFormatter, FileIcons, LoadingScreen } from "@/mixins";
import * as path from "path-browserify";
import FolderSelectorDialog from "@/components/misc/FolderSelectorDialog.vue";

export interface ISnapshotFileTreeFile {
  children?: ISnapshotFileTreeFile[];
  /**
   * Name displayed to the user
   */
  name: string;
  fullPath: string;
}

@Component({
  components: {
    Timeline,
    SnapshotsList,
    FolderSelectorDialog
  }
})
export default class RepositorySnapshotsView extends Mixins(
  LoadingScreen,
  DateFormatter,
  FileIcons
) {
  restoreTargetSelectorOpen = false;
  restoreProgress = 100; // 0-100
  restorePath: string | null = null;

  get restoreRunning() {
    return this.restoreProgress < 100;
  }

  @Watch("restoreProgress")
  onProgressChange() {
    setTimeout(() => {
      requestAnimationFrame(() => {
        if (this.restoreRunning) {
          this.restoreProgress += Math.random() * 4;
          this.restoreProgress *= 1.1;
        } else if (this.restoreProgress !== 100) {
          this.restoreProgress = 100;
        }
      });
    }, Math.random() * 1000);
  }

  @Watch("restoreProgress")
  resetSelection() {
    if (!this.restoreRunning) {
      this.selectedFiles = [];
    }
  }

  restoreToFolder(restorePath: string) {
    this.restoreProgress = 1;
    this.restorePath = restorePath;
  }

  get dedupedSelectedFiles() {
    const deduped: { [idx: string]: true | undefined } = {};
    const f = [...this.selectedFiles].sort(
      (a, b) => a.split("/").length - b.split("/").length
    );

    for (let i = 0; i < f.length; ++i) {
      let dir = f[i];
      let parentDirAlreadySelected = false;
      for (;;) {
        dir = path.dirname(dir);
        if (dir === "/") break;
        if (deduped[dir]) {
          parentDirAlreadySelected = true;
          break;
        }
      }
      if (!parentDirAlreadySelected) {
        deduped[f[i]] = true;
      }
    }

    return Object.keys(deduped);
  }

  get repoId() {
    return Number(routerModule.params.repositoryId);
  }

  get snapshotId() {
    return routerModule.params.snapshotId;
  }

  created() {
    this.setLoading(true);
  }

  async mounted() {
    const response = await repositoriesModule.getSnapshotDetails(
      this.repoId,
      this.snapshotId
    );
    if (response.payload) {
      this.details = response.payload.details;
    }
    this.setLoading(false);
  }

  details: ISnapshotDetails | false = false;

  get repoName() {
    const repo = repositoriesModule.getById(this.repoId);
    return (repo && repo.name) || false;
  }

  selectedFiles: string[] = [];

  get snapshotTree() {
    if (!this.details) return false;
    const tree: ISnapshotFileTreeFile[] = [];
    const parentNodes: ISnapshotFileTreeFile[][] = [tree];
    const files = this.details.files;
    for (let i = 0; i < files.length; ++i) {
      const curFile = files[i];
      const nextFile = files[i + 1];
      const hasChildren = nextFile && nextFile.path.startsWith(curFile.path);

      // -1 for count of /, not substrings split by /; -1 for leading /
      const parentIndex = curFile.path.split("/").length - 2;

      const node: ISnapshotFileTreeFile = {
        name: path.basename(curFile.path),
        fullPath: curFile.path,
        children: hasChildren ? [] : undefined
      };
      if (hasChildren) {
        parentNodes[parentIndex + 1] = node.children!;
      }
      parentNodes[parentIndex].push(node);
    }
    return tree;
  }

  removeSubtree(file: string) {
    this.selectedFiles = this.selectedFiles.filter(
      f => !f.startsWith(file + "/") && f !== file
    );
  }
}
